import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

const router = new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "Default",
      component: () => import("./Default")
    },
    {
      path: "/main-office",
      name: "main-office",
      component: () => import("./components/Greeting")
    },
    {
      path: "/main-guide",
      name: "main-guide",
      component: () => import("./components/Greeting-Guide")
    },
    {
      path: "/trip",
      name: "trip",
      component: () => import("./components/Trips/Trip")
    },
    {
      path: "/tutorials/:id",
      name: "tutorial-details",
      component: () => import("./components/Tutorial")
    },
    {
      path: "/add",
      name: "add",
      component: () => import("./components/AddTutorial")
    },
    {
      path: "/tutorials-page",
      name: "tutorials-page",
      component: () => import("./components/TutorialsPage")
    },
    {
      path: "/tutorials-list",
      alias: "/tutorials",
      name: "tutorials",
      component: () => import("./components/TutorialsList")
    },
    {
      path: "/zaui",
      name: "zaui",
      component: () => import("./components/Zaui")
    }
  ]
});

export default router;